image {
  display: block;
}
//-----动画 -s
@keyframes biling {
  from {
    left: -100%;
  }
  to {
    left: 120%;
  }
}
.biling {
  position: relative;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    z-index: 10;
    left: -40%;
    width: 50%;
    height: 100%;
    opacity: 0.5;
    background-image: linear-gradient(90deg,rgba(255,255,255,0.1),rgba(255,255,255,0.5),rgba(255,255,255,0.1));
    transform: skewX(-25deg);
    animation: 1.5s biling linear infinite;
  }
}
//-----动画 -e

//-----伪元素 -s
.arrow {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    right: -10rpx;
    transform: translate(0, -50%) scaleX(1.5) rotateZ(45deg);
    width: 18rpx;
    height: 18rpx;
    border: 2rpx solid #232323;
    border-radius: 10rpx;
    border-color: #232323 #232323 transparent transparent;
  }
}
